import { createSlice } from '@reduxjs/toolkit'
export const reducer = createSlice({
    name: 'reducerDataLogin',
    initialState: {},//ch ref dc n obj?
    reducers: { //tuong tu action.type
        setLoginReducer: (state, action) => {//state o function ny s ref vo initialState
            if (action.payload == undefined)//ko c data  ref vo th logout
                return {}
            const {
                name,
                email,
                image,
                adress,
                iscollab
            } = action.payload
            state.name = name
            state.email = email
            state.image = image
            state.adress = adress
            state.iscollab = iscollab
        },
    } //export properties ref den func trong var reducer ra  s dng trong func dispatch
})
export const { setLoginReducer, setLogoutReducer } = reducer.actions
export default reducer.reducer