import { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import objStyle from './index.module.scss'
import objGlobalStyle from '../../../GlobalStyle/index.module.scss'
import classNames from 'classnames/bind'
import { Link } from 'react-router-dom';
import Tippy from '@tippyjs/react/headless';
import { useDispatch, useSelector } from 'react-redux';
import { setLoginReducer } from '../../../redux/reducerLogin';
import { Logout as LogoutAuth } from '../../../services/userServices';
function NavHeader() {
    let cv = classNames.bind(objStyle)
    let cv2 = classNames.bind(objGlobalStyle)
    let dispatch = useDispatch();
    let userLogged = useSelector((state) => {
        return state.dataLogged;
    })
    let Logout = async () => {
        await LogoutAuth();
        localStorage.setItem('access_token', '')
        dispatch(setLoginReducer())
    }
    const userExist = Object.keys(userLogged).length
    return (
        <div className={cv('wrapper-nav')}>
            <div className={cv('content-nav1')}>
                {userLogged.iscollab &&
                    <span>
                        <a href='/'>Knh ngi bn</a>
                    </span>
                }
                {!userLogged.iscollab &&
                    <span>
                        <Link to='/userShop'>Tr thnh Ngi bn Shopee</Link>
                    </span>
                }
                <span>
                    <a href='/'>Ti ng dng</a>
                </span>
                <div>
                    <span>
                        Kt ni
                    </span>
                </div>
            </div>
            <div className={cv('content-nav2')}>
                <span type='button'>
                    Thng bo
                </span>
                <span>
                    <a href='/'>H tr</a>
                </span>
                <span type='button'>
                    Ting Vit
                </span>
                {
                    (userExist) ? (
                        <Tippy
                            interactive
                            delay={[null, 50]}
                            render={attrs => (
                                <div className={cv2('showbox') + ' ' + cv('pd-5-10')}>
                                    <div>test</div>
                                    <div>test2</div>
                                    <div onClick={Logout}>Logout
                                        <i class="fa-solid fa-arrow-right-from-bracket"></i>
                                    </div>
                                </div>
                            )}
                        >
                            <div className={cv('user')}>
                                <span>
                                    <i class="fa-regular fa-user"></i>
                                    {userLogged.name}
                                </span>
                            </div>
                        </Tippy>

                    ) : (
                        <>
                            <span>
                                <Link to='/login'>ng k</Link>
                            </span>
                            <span>
                                <Link to='/login'>ng nhp</Link>
                            </span>
                        </>
                    )
                }
            </div >
        </div >
    );
}

export default NavHeader;