import { useState, useEffect } from 'react';
import axios from 'axios';
import classNames from 'classnames/bind'
import Slider from "react-slick";
import objStyle from './index.module.scss'
import { LeftArrow, RightArrow } from '../../home/section';
import Star from './star';
function Section1(obj) {
    let cv = classNames.bind(objStyle);
    let [stateInput, setStateInput] = useState(1);
    let increment = () => {
        setStateInput(stateInput + 1)
    }
    let decrement = () => {
        if (stateInput > 1)
            setStateInput(stateInput - 1)
    }
    let incrementByAmount = (e) => {
        let isNumber = Number.isSafeInteger(Number(e.target.value))
        console.log(typeof (isNumber))
        if (isNumber) {
            setStateInput(Number(e.target.value)) //neu input a2 s error
        }
    }
    let settings = {
        dots: false,
        infinite: false,
        speed: 600,
        slidesToShow: 5,
        slidesToScroll: 1,
        prevArrow: <LeftArrow className1={cv('slick-arrow') + ' ' + cv('left')} />, //properties ref vo s thm vo cc properties c sn vo funcreturnvelement
        nextArrow: <RightArrow className1={cv('slick-arrow') + ' ' + cv('right')} /> //properties ref vo s thm vo cc properties c sn vo funcreturnvelement

    };
    let arrImg = [];
    for (let i = 0; i < 20; i++) {
        arrImg.push(1)
    }
    let listImgSlider = () => {
        return arrImg.map(() => {
            return (
                <div className={cv('item-img')}>
                    <img src='./DetailProduct/Section1-img/img1.jpg' />
                </div>
            )
        })
    }
    return (
        <div className={cv('section-1')}>
            <div className={cv('section-1-image')}>
                <img src='./DetailProduct/Section1-img/img1.jpg' />
                <div className={cv('section-1-image-slider')}>
                    <Slider {...settings}>
                        {listImgSlider()}
                    </Slider>
                </div>
            </div>
            <div className={cv('section-1-detail-product')}>
                <div className={cv('title')}>
                    B Thun polo thch hp mc i chi, d ngoi phong cch Ulzzang AO11
                </div>
                <div className={cv('wrap-flex')}>
                    <div className={cv('wrap-star')}>
                        <span>5.0</span>
                        <Star
                            rating={2}
                        />
                    </div>
                    <div className={cv('wrap-feedback')}>
                        <span>7 nh gi</span>
                    </div>
                    <div className={cv('wrap-sold')}>
                        <span>95  bn</span>
                    </div>
                </div>
                <div className={cv('wrap-select')}>
                    <div className={cv('wrap-price')}>
                        <div>70.000</div>
                    </div>

                    <div className={cv('wrap-flex') + ' ' + cv('flex-gap')}>
                        <div className={cv('gray-color')}>
                            Bo Him
                        </div>
                        <div>Bo him Thi trang</div>
                        <div>Tm hiu thm</div>
                    </div>
                    <div className={cv('wrap-flex') + ' ' + cv('flex-gap')}>
                        <div className={cv('gray-color')}>
                            Vn Chuyn
                        </div>
                        <div>Min ph vn chuyn</div>
                    </div>
                    <div className={cv('wrap-flex') + ' ' + cv('flex-gap')}>
                        <div className={cv('gray-color')}>
                            S Lng
                        </div>
                        <div className={cv('amount')}>
                            <button onClick={decrement} >-</button>
                            <input
                                onChange={(e) => incrementByAmount(e)}
                                value={stateInput}
                            />
                            <button onClick={increment}>+</button>
                        </div>
                        <div>60041 sn phm c sn</div>
                    </div>
                    <div>
                        <button className={cv('btn1')}>Thm vo gi hng</button>
                        <button className={cv('btn2')}>Mua ngay</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Section1;