import { useState, useEffect } from 'react';
import objStyle from './index.module.scss'
import classNames from 'classnames/bind'
import { useDispatch } from 'react-redux';
import SlideLogin from '../Slide';
import SignIn from '../Sign-in';
import SignUp from '../Sign-up';
import { changeLabelReducer } from '../../../redux/reducerChangeLabelLogin';
function WrapperLogin() {
    let cv = classNames.bind(objStyle)
    let dispatch = useDispatch()
    var [state, setState] = useState(true)
    var ElementForm = '';
    if (state)
        ElementForm = SignIn;
    else
        ElementForm = SignUp;
    var switchForm = () => {
        dispatch(changeLabelReducer())
        setState(!state)
    }
    //nu ghi state++ s create 1 static vi value state hin ti sau  reload v ref vo sau khi ref state mi ++,phi click 1 ln na  create static v value lc ny s l 1(chhuyn dc form)
    return (
        <div className={cv('wrapper')}>
            <SlideLogin></SlideLogin>
            <ElementForm switchForm={switchForm}></ElementForm>
        </div>
    );
}

export default WrapperLogin;