import { createSlice } from '@reduxjs/toolkit'
const dataStateWeb = {
    checkStickyHeader: true
}
export const reducer = createSlice({
    name: 'reducerCheckStickyHeader',
    initialState: dataStateWeb,//ch ref dc n obj?
    reducers: { //tuong tu action.type
        unStickyHeader: (state, action) => { //chnh tn func ny s l type nu nh trong reducer cu
            console.log(action)
            state.checkStickyHeader = false
        },
        StickyHeader: (state) => {
            state.checkStickyHeader = true
        },
    } //export properties ref den func trong var reducer ra  s dng trong func dispatch
})
export const { unStickyHeader, StickyHeader } = reducer.actions
export default reducer.reducer

