import { createSlice } from '@reduxjs/toolkit'
const dataLabel = {
    dataLabel: true
}
export const reducer = createSlice({
    name: 'reducerDataLabel',
    initialState: dataLabel,//ch ref dc n obj?
    reducers: { //tuong tu action.type
        changeLabelReducer: (state) => { //chnh tn func ny s l type nu nh trong reducer cu
            state.dataLabel = !state.dataLabel
        },
    } //export properties ref den func trong var reducer ra  s dng trong func dispatch
})
export const { changeLabelReducer } = reducer.actions
export default reducer.reducer