import { useState, useEffect } from 'react';
import axios from 'axios';
import classNames from 'classnames/bind'
import objStyle from './index.module.scss'
import allproduct from '../../../services/productServices';
let cv = classNames.bind(objStyle);
let count = 0;
function Products() {
    let [stateListProd, setStateListProd] = useState([]);
    // let fetchData = async () => {
    //     let listprod = await allproduct();
    //     setStateListProd(listprod.data);//sau khi element return dc 5s,func  y mi return v staticdata v console.log() ra
    // }// nu s dng setState s reload lin tc,s dng useEffect s ch reload li 1 ln
    // fetchData();//call func v tip tuc chy,s return v element trc,sau  5s sau mi console.log() ra
    useEffect(() => { //useEffect ch chy 1 ln nu c [] (c setState() vn ch chy setState 1 ln)
        async function fetchDataProd() {
            let listprod = (await allproduct()).data; //call func func dc call s chy nhng 5s sau mi return. await ch func return mi ref var vo staticdata
            setStateListProd(listprod.listProduct)
        }
        fetchDataProd();//sau khi va call func s tiep tc chy
    }, [])
    let listProduct = () => {
        return stateListProd.map((item) => {
            return (
                <div className={cv('wrap-product')}>
                    <img src='./Section-Products/img1.jpg' />
                    <div className={cv('wrap-info-product')}>
                        <div className={cv('product-name')}>
                            {item.name}
                        </div>
                        <div className={cv('product-more')}>
                            {/* cc icon discount,.... neu c */}
                        </div>
                        <div className={cv('flex')}>
                            <div className={cv('price-item')}>{item.price}</div>
                            <div className={cv('sold-amount')}>{item.quantity}</div>
                        </div>
                    </div>
                </div>
            )
        })
    }
    return (
        <div className={cv('wrapper')}>
            <div className={cv('text-title')}>
                Gi  hm nay
            </div>
            <div className={cv('wrap-flex')}>
                {listProduct()}
            </div>
        </div>
    );
}

export default Products;